@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap);
:root {
  --cor1: #e9e9f2;
  --cor2: #02afe3;
  --cor3: #027ea3;
  --cor4: #014c63;
  --cor5: #012f3d;
  --cor6: #363636;
}

* {
  padding: 0px;
  margin: 0px;
  font-family: "Poppins", sans-serif;
}

.todo-app {
  background-color: #012f3d;
  background-color: var(--cor5);
  box-shadow: 0 0 0.5em #012f3d;
  box-shadow: 0 0 0.5em var(--cor5);
  width: -webkit-max-content;
  width: max-content;
  padding-left: 5%;
  padding-right: 5%;
  height: -webkit-max-content;
  height: max-content;
  min-height: 60%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  margin-top: 10%;
  margin-bottom: 10%;
  border-radius: 16px;
}

:root {
  --cor1: #e9e9f2;
  --cor2: #02afe3;
  --cor3: #027ea3;
  --cor4: #014c63;
  --cor5: #012f3d;
  --cor6: #363636;
}
.todo-form {
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 100%;
  margin-bottom: 3%;
}

.todo-input {
  background-color: transparent;
  border: 4px solid #02afe3;
  border: 4px solid var(--cor2);
  color: #e9e9f2;
  color: var(--cor1);
  padding: 2%;
  font-size: 24px;
  font-weight: bold;
  width: 70%;
  border-bottom-left-radius: 8px;
  border-top-left-radius: 8px;
}

.todo-input:focus {
  box-shadow: 0 0 0 0;
  outline: 0;
}

.todo-button {
  background-image: linear-gradient(to right, #4facfe 0%, #00f2fe 100%);
  border: none;
  padding: 2%;
  font-weight: bold;
  font-size: 24px;
  width: 30%;
  border-bottom-right-radius: 8px;
  border-top-right-radius: 8px;
}

.edit1 {
  border-color: #f77062;
}

.edit {
  background-image: linear-gradient(to top, #f77062 0%, #fe5196 100%);
}

:root {
  --cor1: #e9e9f2;
  --cor2: #02afe3;
  --cor3: #027ea3;
  --cor4: #014c63;
  --cor5: #012f3d;
  --cor6: #363636;
}

.write {
  color: #363636;
  color: var(--cor6);
  font-size: 24px;
  margin-left: 5%;
}

.todo-row {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 2%;
  background-image: linear-gradient(60deg, #96deda 0%, #50c9c3 100%);
  margin-top: 1%;
  padding-top: 2%;
  padding-bottom: 2%;
  border-radius: 8px;
}

.icons {
  margin-right: 5%;
  cursor: pointer;
  display: flex;
  align-items: center;
  font-size: 24px;
  cursor: pointer;
}

.delete-icon {
  margin-right: 8px;
  color: #e9e9f2;
  color: var(--cor1);
}

.edit-icon {
  color: #e9e9f2;
  color: var(--cor1);
}

